.App {
  max-width: 1280px;
  min-height: 100vh;
  background: #465362;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 0 100px 0 100px;
}

.App__title {
  margin: 30px auto;
  padding: 30px 0 30px 0;
  color: #fff;
  font-size: 50px;
  width: 100%;
  height: 70px;
  background-color: #9fc490;
  text-align: center;
  border-radius: 20px;
}

.App__header {
  width: 100%;
  margin: 0 auto;
}

.App__main {
  width: 100%;
  margin: 0 auto;
}

@media screen and (max-width: 1024px) {

  .App {
    padding: 0 40px 0 40px;
  }
}